.button{border:none;border-radius:1rem;transition:all 300ms ease;letter-spacing:0.02rem;font-size:1rem;background-color:transparent;color:#1d1d1d}.button_add{color:white;padding:0.5rem 1rem;font-size:0.9rem;text-transform:uppercase;background-color:#2e2e2e;margin-bottom:0.4rem;font-weight:bold;box-shadow:0 2px 2px rgba(0,0,0,0.05)}@media (min-width: 640px){.button_add{font-size:1rem;margin-bottom:0.7rem}}.button_trash{background-image:url(data:image/png;base64,);width:1.5rem;height:1.5rem;border-radius:5%;border-color:transparent;background-position:center;background-repeat:no-repeat;background-size:1rem;position:absolute;top:1rem;right:1rem}.button_trash:hover{border:1px solid #e2e2e2}.button_small{background-color:#98c530;padding:0.2rem 1rem;color:#1d1d1d;border-radius:1rem}.button_small:disabled{pointer-events:none;opacity:0.5}.button_small:hover{background-color:#4fb427}.container{display:flex;flex-wrap:wrap;grid-gap:0.5rem;gap:0.5rem}@media (min-width: 640px){.container{padding:1.5rem;grid-gap:0.5rem;gap:0.5rem}}.input{box-shadow:0 2px 2px rgba(0,0,0,0.1);border:1px solid rgba(0,0,0,0.1);padding:1rem;position:relative;margin-bottom:1rem;background-color:white;border-radius:0.3rem}.input__header{text-transform:capitalize;font-family:'PT Sans', sans-serif;margin-bottom:1rem}.input__span{text-transform:lowercase;font-size:0.9em;color:darkgray;font-weight:normal}.input__body{display:flex;flex-direction:column;align-items:flex-start}.input__submit{margin-top:1rem}.input input[type='text'],.input input[type='number'],.input input[type='color'],.input input[type='date'],.input input[type='email']{-webkit-appearance:none;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;border-radius:1rem;border:1px solid darkgray;padding:0.2rem 0.5rem;box-sizing:border-box;height:1.7rem;min-width:6rem}.input input[type='text']:focus,.input input[type='number']:focus,.input input[type='color']:focus,.input input[type='date']:focus,.input input[type='email']